.feedback {
    width: 100vw;
    height: 100vh;
    /* background-image: url(../../img/3_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat; */

}

.feedback_wrapper {
    width: 100vw;
    height: 100vh;
    /* background-color: rgba(0, 0, 0, 20%); */
}


.container {
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    height: 100%;
}

.feedback_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    gap: 85px;
}

.feedback_content a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 218.6px;
    height: 61.7px;
    background: #FED494;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    font-family: 'Merriweather';
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    line-height: 50px;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    transition: all 0.3s linear;
}

.feedback_content a:hover {
    background: #FFFFFF;
    color: #4F4F4F;
}

.left_block_feedback {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 946px;
    height: 412px;
    background: rgba(217, 217, 217, 0.44);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 70px;
    margin-bottom: 90px;
    /* position: relative; */

}

.left_block_feedback h4 {
    font-family: 'Merriweather';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 48px;
    text-align: justify;
    color: #FFFFFF;
    margin-top: 50px;
}

.left_block_feedback p {
    font-family: 'Merriweather';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 32px;
    text-align: justify;
    color: #FFFFFF;
}


.left_block_content_feedback {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.left_block_content_feedback input {
    width: 612px;
    height: 65px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: none;
    outline: none;
    font-size: 24px;
    font-weight: 700;
    padding: 0 10px;
}


.left_block_content_feedback button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 187px;
    height: 63px;
    background: #FED494;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    border-radius: 50px;
    text-decoration: none;
    margin-top: 40px;
    transition: all 0.3s linear;
    color: #4F4F4F;
    outline: none;
    border: none;
    cursor: pointer;
    font-family: 'Merriweather';
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    line-height: 50px;

    color: #4F4F4F;

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.left_block_content_feedback button:hover {
    background: #FFF;
    color: #4F4F4F;
}